<template>
    <div class="placedetail content-box">
        <b-img :src="place.picture1"></b-img>
        <h2>{{ place.name }}</h2>
        <hr>
        <div class="content">{{ place.toldescribe }}</div>
    </div>
</template>

<script>
export default {
  name: 'PlaceDetail',
  mounted() {
      this.getPlace()
  },
  data(){
    return {
      place : [],
    }
  },
  methods:{
    getPlace(){
        this.$ajax.get(process.env.VUE_APP_BACKEND_SERVER +"/api/trips/places/" + this.$route.params.id + "/")
        .then(res => (
            this.place = res.data
        ))
        .catch(err => console.log(err));
    }
  },
}
</script>
